/**************** color_picker ******************/
/**************** css ******************/
.color_picker {
  overflow: hidden;
  cursor: pointer;
  position: relative;
  width: 360px;
  height: 76px;
  margin-top: 10px;
  margin-right: auto;
  margin-left: auto;
  text-align: center;
}
.color_picker.wide {
    width: auto;
}
.color_picker + .color_picker {
    margin-top: 30px;
}
.color_picker .min,
  .color_picker .max {
    position: absolute;
    left: 0%;
    top: -2px;
    z-index: 100;
    width: 4px;
    height: 44px;
    background-color: #1aa0dc;
    pointer-events: none;
}
.color_picker .min .label,
    .color_picker .max .label {
      position: absolute;
      left: 0px;
      bottom: -20px;
      z-index: 100;
      -webkit-transform: translate(-50%, 0%);
      -moz-transform: translate(-50%, 0%);
      -ms-transform: translate(-50%, 0%);
      -o-transform: translate(-50%, 0%);
      transform: translate(-50%, 0%);
}
.color_picker .min {
    left: 0%;
}
.color_picker .min:after {
      content: '';
      position: absolute;
      right: 4px;
      top: 0px;
      z-index: 50;
      width: 360px;
      height: 44px;
      background-color: rgba(24, 25, 25, 0.8);
}
.color_picker .max {
    left: 100%;
}
.color_picker .max:after {
      content: '';
      position: absolute;
      left: 4px;
      top: 0px;
      z-index: 50;
      width: 360px;
      height: 44px;
