/**************** css ******************/
.setting_group {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 10px;
}
.setting {
  position: relative;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  width: 120px;
  height: 120px;
  text-align: center;
  margin: 5px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
}
.setting.active .label {
    bottom: 0px;
    background-color: rgba(49, 50, 51, 0.95);
}
.setting.active .label .description {
      display: block;
}
.setting .label {
    cursor: pointer;
    position: absolute;
    left: 0px;
    right: 0px;
    top: 0px;
    z-index: 1000;
    padding: 0px 5px;
    font-size: 11px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
}
.setting .label .description {
      display: none;
      margin-top: 4px;
      padding: 0px 5px;
      text-align: left;
      line-height: 120%;
}
.setting .slider {
    cursor: pointer;
    position: absolute;
    left: 0px;
    right: 0px;
    top: 15px;
    bottom: 0px;
    z-index: 100;
}
.setting .progress_bar {
    position: absolute;
    left: 0px;
    top: 50%;
    z-index: 50;
    height: 10px;
    background-color: #f00;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
}
.setting .value {
    position: absolute;
    left: 0px;
    right: 0px;
    top: 50%;
    z-index: 90;
    margin-top: 10px;
    font-size: 140%;
    font-weight: bold;
    line-height: 100%;
    -webkit-transform: translate(0%, -50%);
    -moz-transform: translate(0%, -50%);
    -ms-transform: translate(0%, -50%);
    -o-transform: translate(0%, -50%);
    transform: translate(0%, -50%);
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
}
.setting .value .unit {
      display: block;
      color: #666666;
      margin-top: -4px;
      font-size: 11px;
}
.setting.radio_buttons {
    display: flex;
    min-width: 120px;
    width: 100%;
    height: auto;
}
.setting.radio_buttons .buttons {
      margin-top: 20px;
}
.setting.radio_buttons .radio_button {
      display: inline-block;
      -webkit-border-radius: 4px;
      -moz-border-radius: 4px;
      border-radius: 4px;
}
.setting.radio_buttons .radio_button.selected label {
        background-color: #1aa0dc;
}
.setting.radio_buttons .radio_button input {
        display: none;
}
.setting.radio_buttons .radio_button label {
        cursor: pointer;
        display: block;
        padding: 4px 10px;
        -webkit-border-radius: 4px;
        -moz-border-radius: 4px;
        border-radius: 4px;
}

